<template>
  <div class="tianLing">
    <el-descriptions :title="tianLingData.fullName" :column="2" border>
      <el-descriptions-item
        label="在职时间"
        label-class-name="my-label"
        content-class-name="my-content"
      >
        {{ tianLingData.duration }}
      </el-descriptions-item>

      <el-descriptions-item label="所在岗位">
        {{ tianLingData.post }}
      </el-descriptions-item>

      <el-descriptions-item label="工作内容">
        {{ tianLingData.workAbout }}
      </el-descriptions-item>
    </el-descriptions>

    <div class="tianLing-title">项目经历</div>
    <div class="tianLing-project">
      <el-carousel indicator-position="outside" :interval="10000">
        <el-carousel-item v-for="(chunk, index) in resProjectArr" :key="index">
          <div
            class="tianLing-project-public"
            v-for="item in chunk"
            :key="item.id"
          >
            <div :class="item.name.indexOf('App') === -1 ? 'img' : 'imgApp'">
              <img
                @click="showImgDialog(item)"
                :src="`/img/tianLingProject/${item.name}.png` | imgFormat"
                style="width: 100px; height: 100px"
                alt="请刷新重新加载..."
              />
            </div>

            <div class="info">
              <span class="info-name">{{ item.name }}</span>
              <span class="info-time">{{ item.time }}</span>
            </div>
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>

    <!-- 图片预览弹窗 -->
    <div
      class="picBox"
      v-if="showPic"
      :style="{ width: showPicName.indexOf('App') === -1 ? '1200px' : '380px' }"
    >
      <div class="picBox-close">
        <span> {{ showPicName }} </span>
        <i @click="showPic = !showPic" class="el-icon-close"></i>
      </div>
      <img
        :src="`/img/tianLingProject/${showPicName}.png` | imgFormat"
        alt="刷新重新加载..."
      />
    </div>
    <!-- 图片预览弹窗 -->
  </div>
</template>

<script>
export default {
  name: "tianLing",
  components: {},
  props: {
    tianLingData: {
      type: Object,
      default: () => {},
    },
  },
  computed: {
    resProjectArr() {
      const chunkSize = 6; // 每个盒子的项数
      const chunks = [];
      let currentIndex = 0;
      while (currentIndex < this.tianLingData.projectList.length) {
        chunks.push(
          this.tianLingData.projectList.slice(
            currentIndex,
            currentIndex + chunkSize
          )
        );
        currentIndex += chunkSize;
      }
      return chunks;
    },
  },
  data() {
    return {
      showPic: false,
      showPicName: null,
    };
  },
  methods: {
    showImgDialog(item) {
      this.showPic = true;
      this.showPicName = item.name;
    },
  },
  created() {},
  mounted() {},
};
</script>

