<template>
  <div class="friendlyLink">
    <span> 联系我： </span>
    <div v-for="(value, key) in friendlyLinkObj" :key="key">
      <div>
        {{ key }}:
        <a class="friendlyLink-a" :href="value" target="_blank">
          {{ value }}
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import { friendlyLink } from "@/api/allInterface";

export default {
  name: "friendlyLink",
  components: {},
  props: {},
  data() {
    return {
      friendlyLinkObj: {},
    };
  },
  methods: {
    getFriendlyLink() {
      friendlyLink().then((res) => {
        if (res.data.code === 200) {
          this.friendlyLinkObj = res.data.data;
        }
      });
    },
  },
  created() {
    this.getFriendlyLink();
  },
  mounted() {},
};
</script>

